import React, { useState, useEffect } from 'react';
import { supabase } from '../shared/supabaseClient';

const WidgetsModal = ({ setShowModal, selectedDay }) => {
  const [widgets, setWidgets] = useState([]);
  const [widgetName, setWidgetName] = useState('');
  const [widgetStart, setWidgetStart] = useState(0);
  const [widgetEnd, setWidgetEnd] = useState(0);
  const [isCreating, setIsCreating] = useState(false);
  const [loading, setLoading] = useState(true);
  const [userId, setUserId] = useState(null);
  const [isDeleteMode, setIsDeleteMode] = useState(false);

  useEffect(() => {
    const fetchUserAndWidgets = async () => {
      try {
        const { data: { user } } = await supabase.auth.getUser();
        if (!user) throw new Error('No user found.');

        setUserId(user.id);
        const { data: Widgets, error } = await supabase
          .from('Widgets')
          .select("*")
          .eq('user_id', user.id);

        if (error) throw error;

        setWidgets(Widgets || []);
      } catch (error) {
        console.error('Errore nel caricamento dei widget:', error.message);
      } finally {
        setLoading(false);
      }
    };

    fetchUserAndWidgets();
  }, []);

  const handleModalClose = () => setShowModal(false);

  const handleWidgetCreation = async () => {
    if (!widgetName.trim()) {
      alert('Please enter a widget name.');
      return;
    }

    if (!userId) {
      alert('User not authenticated.');
      return;
    }

    const newWidget = { name: widgetName, start_hour: widgetStart, end_hour: widgetEnd, user_id: userId };

    try {
      const { error } = await supabase.from('Widgets').insert([newWidget]);
      if (error) throw error;

      setWidgets(prevWidgets => [...prevWidgets, newWidget]);
      setWidgetName('');
      setWidgetStart(0);
      setWidgetEnd(0);
      setIsCreating(false);
    } catch (error) {
      console.error('Errore nella creazione del widget:', error.message);
    }
  };

  const handleWidgetClick = (widget) => {
    if (isDeleteMode) {
      handleWidgetDeletion(widget.id);
    } else {
      handleWidgetApplication(widget);
    }
  };

  const handleWidgetApplication = (widget) => {
    if (selectedDay) {
      const dayColumn = document.querySelector(`[data-day="${selectedDay.toDateString()}"] .pills-container`);
      if (dayColumn) {
        const pills = dayColumn.querySelectorAll('.hour-pill');
        for (let i = widget.start_hour; i <= widget.end_hour; i++) {
          pills[i].classList.add('locked');
          pills[i].classList.remove('unlocked');
        }
      } else {
        console.error('Colonna del giorno non trovata per la data:', selectedDay.toDateString());
      }
    }
    handleModalClose();
  };

  const handleWidgetDeletion = async (widgetId) => {
    try {
      const { error } = await supabase
        .from('Widgets')
        .delete()
        .eq('id', widgetId);

      if (error) throw error;

      setWidgets(prevWidgets => prevWidgets.filter(widget => widget.id !== widgetId));
    } catch (error) {
      console.error('Errore nella rimozione del widget:', error.message);
    }
  };

  const switchSection = () => {
    setIsCreating(!isCreating);
  };

  const toggleDeleteMode = () => {
    setIsDeleteMode(!isDeleteMode);
  };

  return (
    <div style={styles.modal}>
      <div style={styles.modalContent}>
        <span style={styles.closeButton} onClick={handleModalClose}>
          &times;
        </span>
        {loading ? (
          <p>Caricamento...</p>
        ) : (
          <div>
            {isCreating ? (
              <div>
                <InputField
                  label="Nome del Widget:"
                  value={widgetName}
                  onChange={(e) => setWidgetName(e.target.value)}
                />
                <InputField
                  label="Ora di inizio:"
                  type="number"
                  value={widgetStart}
                  onChange={(e) => setWidgetStart(Number(e.target.value))}
                  min={0}
                  max={23}
                />
                <InputField
                  label="Ora di fine:"
                  type="number"
                  value={widgetEnd}
                  onChange={(e) => setWidgetEnd(Number(e.target.value))}
                  min={0}
                  max={23}
                />
                <Button text="Crea Widget" onClick={handleWidgetCreation} secondary />
                <Button text="Usa Widget" onClick={switchSection} />
              </div>
            ) : (
              <div>
                {widgets.length > 0 && (
                  widgets.map((widget, index) => (
                    <div key={index} style={styles.widgetRow}>
                      <Button
                        text={widget.name}
                        onClick={() => handleWidgetClick(widget)}
                      />
                    </div>
                  ))
                )}
                <Button text="Crea Widget" onClick={switchSection} secondary />
                <Button
                  text={isDeleteMode ? "Applica Widget" : "Elimina Widget"}
                  onClick={toggleDeleteMode}
                  deleteMode={!isDeleteMode} // Swap the modes
                />
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

const InputField = ({ label, ...props }) => (
  <div style={styles.inputWrapper}>
    <label style={styles.label}>{label}</label>
    <input style={styles.input} {...props} />
  </div>
);

const Button = ({ text, onClick, secondary, deleteMode }) => (
  <button
    style={{
      ...styles.button,
      ...(secondary ? styles.buttonSecondary : {}),
      ...(deleteMode ? styles.buttonDelete : {}),
    }}
    onClick={onClick}
  >
    {text}
  </button>
);

const styles = {
  modal: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    position: 'fixed',
    zIndex: 1000,
    left: 0,
    top: 0,
    width: '100%',
    height: '100%',
    overflow: 'auto',
    backgroundColor: 'rgba(0,0,0,0.4)',
  },
  modalContent: {
    backgroundColor: '#fff',
    margin: 'auto',
    padding: '40px 30px 20px 30px',
    border: '1px solid #888',
    width: '80%',
    maxWidth: '500px',
    boxShadow: '0px 0px 10px rgba(0, 0, 0, 0.2)',
    borderRadius: '8px',
    position: 'relative',
    animation: 'fadeIn 0.3s ease-in-out',
  },
  closeButton: {
    position: 'absolute',
    top: '10px',
    right: '10px',
    color: '#aaa',
    fontSize: '28px',
    fontWeight: 'bold',
    cursor: 'pointer',
  },
  inputWrapper: {
    marginBottom: '10px',
  },
  label: {
    display: 'block',
    marginBottom: '5px',
    fontSize: '16px',
  },
  input: {
    width: '100%',
    padding: '8px',
    fontSize: '16px',
    borderRadius: '4px',
    border: '1px solid #ddd',
  },
  button: {
    padding: '10px 20px',
    margin: '10px 0',
    fontSize: '16px',
    cursor: 'pointer',
    backgroundColor: '#4d68fa',
    color: '#fff',
    border: 'none',
    borderRadius: '25px',
    width: '100%',
    transition: 'background-color 0.3s',
  },
  buttonSecondary: {
    backgroundColor: '#f0f0f0',
    color: '#333',
  },
  buttonDelete: {
    backgroundColor: '#ff4d4d',
    color: '#fff',
  },
  widgetRow: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
};

export default WidgetsModal;
